                    title: '你好',
                    content: '确定要删除图片吗？',
                    cancelText: '取消',
                    confirmText: '确定',
                    success: res => {
                        if (res.confirm) {
                                this.idCardImageList.splice(e.currentTarget.dataset.index, 1)
                        }
                    }
                })
            },
            ChooseCertificateImage(e) {
                uni.chooseImage({
                    count: 1, //默认9
                    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
